













































import { SYMBOLS } from '@/helpers';
import { Settings } from '@/types';
import {
  Component,
  Emit,
  Model,
  Prop,
  Vue,
  Watch,
} from 'vue-property-decorator';

@Component
export default class Options extends Vue {
  private SYMBOLS: string = SYMBOLS.split('').join(' ');

  @Model('change')
  private settings!: Partial<Settings>;
}
